
<template>
    <div class="add-property pb-2">
      <div>
        <div class="px-3 back_close d-flex justify-content-between align-items-center py-3 d-md-none">
          <b-icon v-if="dir === 'ltr'" font-scale="1.5" icon="arrow-left" @click="$router.go(-1)"></b-icon>
          <b-icon v-else font-scale="1.5" icon="arrow-right" @click="$router.go(-1)"></b-icon>
          <b-icon icon="x-lg" @click="$router.push(localePath('/'))"></b-icon>
        </div>
        <div class="post-form-container">
          <!-- <ProgressCircle :current-step="1" :total-steps="5"></ProgressCircle> -->
          <b-progress class="w-100 mt-lg-2" :value="1" :max="5" :height="progressHeight"></b-progress>
          <div class="px-3 step">
            <PropertyCategory :isAdd="false" @nextStep="goNext" />
          </div>
        </div>
      </div>
      <WarningModal
        :show="showWarningModal"
        :routeTo="routeTo"
        @closeModal="showWarningModal = false"
        @closeModalAndNavigate="closeModalAndNavigate" />
    </div>
  </template>
  
  <script>
  import PropertyCategory from '@/components/postingRequestForm/PropertyCategory';
  import ProgressCircle from '@/components/postingRequestForm/ProgressCircle';
  import WarningModal from '@/components/postingRequestForm/WarningModal';
  import { BootstrapVue, BootstrapVueIcons, BIcon, BIconArrowUp, BIconArrowDown } from 'bootstrap-vue'

  export default {
    transition: {
      name: 'slide-fade',
      mode: 'out-in',
    },
    components: {
      BootstrapVueIcons,
      BIcon,
      BIconArrowUp,
      BIconArrowDown,
      PropertyCategory,
      ProgressCircle,
      WarningModal,
    },
    data() {
      return {
        currentRequest: null,
        showWarningModal: false,
        routeTo: '',
        navigate: false,
      };
    },
    computed:{
      dir() {
        return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
      },
      appEnv(){
        return this.$config.APP_ENV
      },
      progressHeight(){
        if(process.client){
          if(window.innerWidth <= 991){
            return '3px'
          }else{
            return '5px'
          }
        }
      }
    },
    head() {
      return {
        title: `${this.dir === "ltr" ? "Posting Request | Step 1" : "إختيار تصنيف العقار | الخطوة 1"} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
        meta: [
          // hid is used as unique identifier. Do not use `vmid` for it as it will not work
          {
            hid: 'description',
            name: 'description',
            content: this.dir === "ltr" ? 'Posting Request | Property Category' : 'نشر طلب | تصنيف العقار'
          }
        ]
      }
    },
    methods: {
      goNext(values) {
        localStorage.setItem(
          'request',
          JSON.stringify({
            ...JSON.parse(this.currentRequest),
            propertyCategory: values, // Here Binding The Comming Value of the selected property category 
          })
        );
        this.$router.push(this.localePath('/requests/post/destination'));
      },
      closeModalAndNavigate() {
        this.showWarningModal = false;
        this.navigate = true;
      },
    },
    mounted() {
      this.currentRequest = localStorage.getItem('request');
    },
    beforeRouteLeave(to, from, next) {
      if (!to.path.includes('/requests/post/') && this.currentRequest) {
        this.showWarningModal = true;
        this.routeTo = to.path;
  
        if (this.navigate) return next();
      } else next();
    },
  };
  </script>
  
<style scoped>
  .back_close {
    background-color: #f2f2f2 !important;
  }
</style>

