<template>
    <!-- headeing -->
    <div class="row property-category">
      <b-col cols="12" class="step-heading">
        <h1 class="second-step__title font-weight-semi font-size-large text-center py-4">
          {{ $t('form.postingRequest.category.heading') }}
        </h1>
      </b-col>
      <b-overlay :show="show" :no-wrap="true"></b-overlay>
      <template v-for="(tag, index) in tags">
        <b-col v-b-toggle="`collapse-${index + 1}`" cols="12" class="mt-3 step-content bg-white pt-3 mx-auto" v-if="tag.tags.length" :key="tag.id">
          <div class="collapse_header mb-3 d-flex justify-content-between">
            <h3 class="mb-0 font-weight-bold">{{ tag.category_name }}</h3>
            <b-icon font-scale="1.5" icon="chevron-down"></b-icon>  
            <!-- <b-icon font-scale="1.5" v-else icon="chevron-up"></b-icon>   -->
          </div>
          <b-collapse :id="`collapse-${index + 1}`">
            <b-form-radio-group v-model="selected">
                <b-row>
                  <b-col v-for="innerTag in tag.tags" :key="innerTag + tag.id" cols="12" sm="6" lg="3" @click.stop="selectTag(innerTag)">
                    <b-form-radio :value="innerTag" :class="dir === 'ltr' ? 'for_en' : 'for_ar'">
                      <b-button size="sm" class="border-0 bg-transparent d-flex align-items-center gap">
                        <img width="20px" height="40px" :src="innerTag.icon">
                        <p class="d-flex flex-column category-details">
                          <span>{{ innerTag.name }}</span>
                          <span>
                            (<span v-for="(period, index) of innerTag.price_type" :key="period">{{  `${formating_price_type(period)} ${index !== innerTag.price_type.length - 1 ? '/' : ''}` }}</span>)
                          </span>
                        </p>
                      </b-button>
                    </b-form-radio>
                  </b-col>
                </b-row>
            </b-form-radio-group>
          </b-collapse>
        </b-col>
      </template>

      <b-col v-if="isAdd" cols="12" class="my-3 step-content bg-white pt-3 mx-auto">
        <h3 class="font-weight-bold"> {{ this.dir === 'ltr' ? 'Other Tags' : 'تاجات اخرى' }} <span class="font-weight-normal text-dark small">{{ this.dir === 'ltr' ? '(Optional)' : '(إختيارى)' }}</span></h3>
        <chip-selector :options="suggestions" @update-selection="updateSuggestions"></chip-selector>
      </b-col>

      <b-col cols="12" class="mt-4" v-if="showNext">
        <div class="step-next-container">
          <b-button variant="primary" @click="handleNextClick" class="next-btn">
            <span>{{ $t('common.next') }}</span>
          </b-button>
        </div>
      </b-col>
    </div>
  </template>
  
  <script>
  import axios from 'axios'
  import { BootstrapVue, BootstrapVueIcons, BIcon, BIconArrowUp, BIconArrowDown } from 'bootstrap-vue'
  export default {
    name: 'PropertyCategory',
    components: {
      BootstrapVueIcons,
      BIcon,
      BIconArrowUp,
      BIconArrowDown,
      ChipSelector: () => import('~/components/shared/form/chipSelector'),
    },
    props: {
      showNext: {
        type: Boolean,
        default: () => true,
      },
      isAdd: {
        type: Boolean,
        default: true,
      }
    },
    data() {
      return {
        tags: null,
        selected: null,
        show: false,
        suggestions: [{label: 'Short Term', value: 1}, {label: 'Vacation', value: 2}, {label: 'Office', value: 3}, {label: 'Clinic', value: 4}],
      };
    },
    computed: {
      dir() {
        return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
      },
    },
    methods: {
      handleNextClick() {
        if (this.selected === '' || this.selected === null) {
          window.scrollTo({
            top: 120,
            left: 0,
            behavior: 'smooth',
          });
          let body = this.$t('form.checkListFirst');
          this.$bvToast.toast(body, {
            title: this.$t('form.fillInput'),
            variant: 'primary',
            toaster: 'b-toaster-top-center',
            solid: true,
            autoHideDelay: 1000,
          });
          return;
        }
        this.$emit('nextStep', this.selected); // Add Selected Value Here whether value only or the whole object 
      },
      selectTag(tag){
        if(!!this.selected){
          if(tag.id !== this.selected.id){
            this.show = true;
            this.selected = tag;
            localStorage.setItem('tag', JSON.stringify(this.selected));
            localStorage.removeItem('request')
            location.reload();
            localStorage.setItem('request', JSON.stringify({ propertyCategory: JSON.parse(localStorage.getItem('tag')) }))
            localStorage.removeItem('tag')
            this.show = false;
          }
        }else{
          this.selected = tag;
        }
      },
      formating_price_type(item){
        switch (item){
          case 'dy':
            return this.$t('form.postingRequest.stayingDetails.days')
          case 'my':
            return this.$t('form.postingRequest.stayingDetails.months')
          case 'yy':
            return this.$t('form.postingRequest.stayingDetails.years')
          case 'hr':
            return this.$t('form.postingRequest.stayingDetails.hours')
          case 'wy':
            return this.$t('form.postingRequest.stayingDetails.weeks')
          default:
            return false  
        }
      },
      updateSuggestions(val){
        console.log(val);
      }
    },
    created(){
      this.show = true;
    },
    async mounted() {
      const res = await axios.get('https://apibeta.rentup.co/api/tags');
      console.log(res)
      this.tags = res.data.Data;
      this.show = false;
      window.scrollTo({
        top: 0,
        left: 0,
        behavior: 'smooth',
      });
      const request = localStorage.getItem('request') && JSON.parse(localStorage.getItem('request'));
      if(request){
        this.selected = request.propertyCategory;
      }
    },
  };
  </script>
  
  <style lang="scss">
.gap {
  gap: 10px;
}
.custom-control.custom-radio {
  border: 1px solid #333;
  border-radius: 10px;
  padding: 10px 0 10px 10px;
  width: 100% !important;
  .category-details {
    font-size: 11px;
    font-weight: bold;
    line-height: 14px;
  }
  .custom-control-label {
    position: static;
  }
  &.for_en {
    .custom-control-label::before, .custom-control-label::after{
      left: auto;
      right: 15px;
      top: 20px;
    }
  }
  &.for_ar {
    .custom-control-label::before, .custom-control-label::after{
      right: auto;
      left: 15px;
      top: 20px;
    }
  }
}

  .custom-control {
    margin-bottom: 16px;
  }

  .property-category {
    margin-bottom: 100px !important;
    .row {
      align-items: center;
    }
  }
  .step-content {
    .divider {
      width: 120%;
      height: 5px;
      position: relative;
      left: -30px;
      background-color: #f7f9fa;
    }
  }
  
  </style>
